import 'uni-pages';import Vue from 'vue'
import App from './App'
// 导入网络请求的包
import {
	$http
} from '@escook/request-miniprogram'

uni.$http = $http





// 请求开始之前做一些事情
$http.beforeRequest = function(options) {
	uni.showLoading({
		title: '数据加载中...'
	})
}
// 封装弹窗的方法
uni.$showMsg = function(title = '数据请求失败', duration = 1500){
	uni.showToast({
		title,
		duration,
		icon:'none'
	})
}
// 请求完成之后做一些事情
$http.afterRequest = function() {
	wx.hideLoading()
}


Vue.config.productionTip = false

App.mpType = 'app'

const app = new Vue({
	...App
})
app.$mount()